.ChatHistory{
    margin-left: 5rem;
    background-color: lightblue;
    color: black;
    margin-bottom: 2rem;
    display: block;
    width: 20rem;
    height: 20rem;
    border: 4px solid #fff;
    border-radius: 16px;
    box-shadow: 16px 16px 8px gray;
    overflow-y: scroll;
}
.ChatHistory::-webkit-scrollbar{
    display: none;
}
.ChatInput input{
    border: 4px solid red;
    border-radius: 16px;
    width: 400px;
    height: 50px;
}
.header{
    margin-top: -5rem;
    background-color: lightblue;
    color: black;
    margin-bottom: 2rem;
    display: black;
    width: 500px;
    margin-left: 0.5rem;
    border: 4px solid #fff;
    border-radius: 16px;
    box-shadow: 16px 16px 8px gray;
}
.App{
    margin: 1rem auto;
    width: 28rem;
    height: 25rem;
}
.middle{
    display: flex;
    justify-content: space-between;
    min-height: 70vh;
    flex-direction: column;
    align-items: center;
}